<template>
  <div class="mall">
    <div class="lunbo">
      <form action="/">
        <van-search
          class="mall-search"
          placeholder="搜索"
          show-action
          @focus="toSearch"
        />
      </form>
      <div class="swiper-container">
        <div class="swiper-wrapper">
          <div class="swiper-slide" v-for="item in banner" :key="item.id" >
            <img :src="item.banner " alt="">
          </div>
        </div>
        <div class="swiper-pagination"></div>
      </div>
    </div>
    <div class="self_type">
      <router-link tag="div" to="scKind">
        <img src="./../assets/img/lipinfenlei.png" alt="">
        <h2>礼品分类</h2>
      </router-link>
      <router-link tag="div"  to="jfMingxi">
        <img src="./../assets/img/wodejifen.png" alt="">
        <h2>我的积分</h2>
      </router-link>
    </div>
    <div class="mingping">
      <img class="m_logo" src="../assets/img/mingpintehui.png" alt="">
      <div class="topThree">
        <router-link v-if="preferential[0]" tag="div" :to="{name:'spXiangqin',params:{id:preferential[0].id}}"  class="big_img">
          <img :src="preferential[0].banner " alt="">
        </router-link>
        <div class="samll_img">
          <img v-if="preferential[1]" :src="preferential[1].banner" @click="()=>{this.$router.push({name:'spXiangqin',params:{id:preferential[1].id}})}" alt="">
          <img v-if="preferential[2]"  :src="preferential[2].banner" @click="()=>{this.$router.push({name:'spXiangqin',params:{id:preferential[1].id}})}" alt="">
        </div>
      </div>
    </div>
    <div class="bottom">
      <ul>
        <router-link tag="li" v-for="item in list" :key="item.id" :to="{name:'spXiangqin',params:{id:item.id}}">
          <img v-if="item.goods_img&&item.goods_img[0]" :src="item.goods_img[0]" alt="">
        </router-link>

      </ul>
    </div>
    <a href="" class="kefu iconfont icon-kefu"></a>
  </div>
</template>

<script>
  import Swiper from 'swiper'
  import { apiGet } from '../common/js/api';
    export default {
        name: "Mall",
      data(){
          return{
            banner:[
              {
                detail_url:require("./img/timg.jpg"),
                id:0
              },
              {
                detail_url:require("./img/timg.jpg"),
                id:1
              },
              {
                detail_url:require("./img/timg.jpg"),
                id:2
              },
            ],
            preferential:[
              {
                goods_img:require("./img/timg.jpg"),
                id:3
              },
              {
                goods_img:require("./img/timg.jpg"),
                id:4
              },
              {
                goods_img:require("./img/timg.jpg"),
                id:5
              }
            ],
            list:[
              {
                goods_img:require("./img/timg.jpg"),
                id:6
              },
              {
                goods_img:require("./img/timg.jpg"),
                id:7
              },
              {
                goods_img:require("./img/timg.jpg"),
                id:8
              },
              {
                goods_img:require("./img/timg.jpg"),
                id:9
              },
            ]
          }
      },
      created(){
        this.getMallData()
      },
      mounted(){
        var mySwiper = new Swiper('.swiper-container', {
          autoplay: true,//可选选项，自动滑动
          loop : true,
          pagination: {
            el: '.swiper-pagination',
          },
        })
      },
      methods:{
        toSearch(){
          this.$router.push({name:'searchList'});
        },
        //获取首页数据
        getMallData(){
          apiGet('/goods/index',{

          }).then(res=>{
            this.banner = res.data.data.banner
            this.preferential = res.data.data.preferential
            console.log(this.preferential);
            this.list = res.data.data.list
          })
        }
      }
    }
</script>

<style lang="scss">
  .swiper-pagination-bullet-active{
    width: 0.13rem !important;
    background: white !important;
    height: 0.06rem;
    border-radius: 0.03rem;
  }
  .swiper-container-horizontal>.swiper-pagination-bullets, .swiper-pagination-custom, .swiper-pagination-fraction{
    bottom: 0.6rem;
  }
  .van-search__action{
    display: none;
  }
  .mall {
    height: 100%;
    overflow: auto;
    background: #E6E9ED;
    margin-bottom: 0.4rem;
    .van-search__action{display: none}
    .lunbo{
      position: relative;
        .van-search{
          position: absolute;
          top:0;
          width: 100%;
          padding-right:.13rem;
          background: transparent !important;
          z-index: 30;
          .van-search__action{display: none !important}
          .van-field__control{
            line-height: 24px !important;
          }
          .van-field__body{
            height: 100%!important;
          }
          input{
            height: 0.3rem;
          }
        }

      
      .swiper-container{
        width: 100%;
        .swiper-slide>img{
          width: 100%;
          max-height: 2.56rem;
        }
      }
    }
    .self_type{
     width: 3.5rem;
      margin: 0rem auto 0;
      border-radius:0.04rem;
      height: 1.04rem;
      position: relative;
      z-index: 3;
      top: -0.52rem;
      display: flex;
      div{
        width: 1.75rem;
        flex: 1;
        border-radius: 0;
        background-size: 1.75rem 1.04rem;
        overflow: hidden;
       img{
         width: 0.26rem;
         height: 0.29rem;
         display: block;
         margin: 0.24rem auto 0.1rem;
       }
        h2{
          font-size: 0.17rem;
          color: #333;
          text-align: center;
        }
        &:nth-of-type(1){
          background-image:url("./../assets/img/lipinfenlei--bg.png");
        }
        &:nth-of-type(2){
          background-image: url("./../assets/img/wodejifen--bg.png");
        }
      }
    }
    .mingping{
      width: 3.5rem;
      height: 2.67rem;
      background: white;
      margin: 0 auto;
      position: relative;
      overflow: hidden;
      top:-0.4rem;
      .m_logo{
        width: 0.64rem;
        height: 0.31rem;
        margin: 0.2rem auto;
        display: block;
      }
      .topThree{
        display: flex;
        justify-content: space-around;
        .big_img{
          width: 1.56rem;
          height: 1.74rem;
          img{
            width: 1.56rem;
            height: 1.74rem;
          }
        }
        .samll_img{
          width: 1.56rem;
          display: flex;
          flex-direction: column;
          justify-content: space-between;
          img{
            width: 1.56rem;
            height: 0.81rem;
          }
         }
      }


    }
    .bottom{
      width: 3.5rem;
      padding: 0.21rem 0.13rem 0;
      border-radius: 0.04rem;
      position: relative;
      top:-0.4rem;
      background: white;
      overflow: auto;
      &::-webkit-scrollbar{display: none;};
      white-space: nowrap;
      margin: 0.17rem auto 0.2rem;
      ul{
        height: 1.28rem;
      }
      li{
        width: 0.87rem;
        height: 0.87rem;
        border-radius: 0.04rem;
        display: inline-block;
        margin-right: 0.13rem;
        img{
          width: 0.87rem;
          height: 0.87rem;
        }
      }
    }
  }
  .kefu{
    width: 0.52rem;
    height: 0.52rem;
    background:linear-gradient(-90deg,rgba(70,133,238,1),rgba(155,193,255,1));
    border-radius:50%;
    box-shadow:0px 5px 10px 0px rgba(60,127,238,0.42);
    position: fixed;
    bottom: 1.5rem;
    display: block;
    text-align: center;
    line-height: 0.52rem;
    font-size: 0.28rem;
    color: white;
    z-index: 3;
    right: 0.11rem;
  }
</style>



